/* Toggle */
.Component-switchBase-10.Component-checked-13 + .Component-track-12 {
  background-color: #a44e48 !important;
}
.Component-switchBase-10.Component-checked-13 + .Component-track-12::after {
  margin-right: 10px;
  margin-top: 3px;
  display: block;
  color: #fff;
  font-size: 12px;
  content: "ON" !important;
}
.Component-switchBase-10.Component-checked-13 {
  transform: translateX(33px) !important;
}
.Component-thumb-11 {
  width: 21px !important;
  height: 21px !important;
}
.Component-switchBase-10 {
  padding: 2px;
}
.MuiSwitch-switchBase {
  left: 3px !important;
}
.Component-root-9 {
  width: 60px !important;
  height: 23px !important;
  margin: 0 !important;
  padding: 0;
}
.MuiFormControlLabel-root {
  margin: 0 !important;
}

/* custom */

.big-visa img {
  width: 100%;
  height: 190px;
}
.big-visa .form-h {
  position: absolute;
}
.big-visa input {
  background-color: #f7f8f908;
  font-size: 21px;
  color: #fff;
  border: 1px solid #6b4650;
  padding: 5px;
  border-radius: 4px;
}

.small-control {
  background-color: #2c141b;
  border-radius: 7px;
  padding: 20px 10px;
  margin-top: 10px;
}
.small-control img {
  width: 35px;
}
.small-control h5 {
  font-size: 15px;
  margin-bottom: 0;
  margin-top: 10px;
}
.small-control p {
  font-size: 12px;
  margin-top: 2px;
  margin-bottom: 10px;
  color: rgba(190, 190, 190, 0.699);
}
.img-icon {
  width: 15px !important;
  height: 16px !important;
  margin-top: 8px;
  margin-right: -1px;
  position: relative;
}
.card-number {
  width: 45%;
  height: 37px;
  border-radius: 4px;
  margin-left: -22px;
  padding-left: 27px !important;
  font-size: 16px !important;
  text-align: center;
}
.form-ic {
  top: 110px;
  width: 45%;
}
.form-h {
  text-align: left;
}
.big-visa .form-h1 {
  top: 64px;
  left: 38px;
}
.form-h2 {
  left: 48px;
}
.form-h3 {
  left: 53%;
}
.big-visa label {
  font-size: 11px;
  margin-top: 8px;
  margin-bottom: 3px;
  margin-left: -7px;
}
.card-input {
  width: 233px;
  border: 1px solid #6b4650;
  background-color: transparent !important;
}
.main-visa {
  max-width: 100%;
  max-height: 190px;
  margin: auto;
}
.big-visa .form-h1 input[disabled] {
  border: 0;
  background-color: transparent;
}
.total-visa {
  background-color: #361b22;
  padding: 18px;
  border-radius: 10px;
  box-shadow: 1px 1px 18px -9px #000000;
}
.btn-visa {
  color: rgb(255, 254, 254);
  border: 0;
  padding: 5px 15px 5px 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  font-size: 14px;
}
.btn-edit {
  background-color: #ae625a;
}
.btn-edit:hover {
  background-color: rgb(206, 117, 108);
}
.btn-save {
  background-color: #eb5e70;
}
.btn-save:hover {
  background-color: #cc5161;
}
.card-input:disabled {
  border: none !important;
}
