.list-content i {
  margin-right: 5px;
  margin-top: 10px;
  cursor: pointer;
  width: 10%;
  float: right;
  color: #969595;
  text-align: right;
  padding-right: 10px;
}
.list-content input:focus {
  border: none !important;
  outline: none !important;
}
.keys li {
  background-color: #361b22;
  overflow: hidden;
  border-radius: 10px;
}

.keys li i:hover {
  color: #cecbcb;
}
.keys ul {
  padding: 0;
}
.l-key {
  padding: 20px;
  padding: 30px 15px;
}
.l-key label {
  color: #ffffff7a;
  font-size: 13px;
  margin-bottom: 10px;
}
.list-content input {
  background-color: #281218;
  border: 0;
  color: #cccaca;
  padding-left: 10px;
}
.list-content {
  background-color: #281218;
  width: 100%;
  border-radius: 0 5px 5px 0;
}
.deactivated {
  font-size: 10px;
  padding: 6px 10px;
  border-radius: 16px;
  border: 0;
  margin-left: 10px;
  background-color: #a44e48;
  color: #fff;
  margin-top: 4px;
  width: 65px;
}
.deactivated:hover {
  background-color: #992f28;
}
.img-key {
  width: 38px;
}
.field {
  height: 37px;
  width: 80%;
}
#key {
  width: 85%;
  margin-top: 6px;
}
.left-disc {
  width: 85%;
}
ul {
  padding: 1rem;
  margin-bottom: 0 !important;
}
.dis-id {
  color: #ffffff7a;
  font-size: 13px;
}
.name {
  color: #fff;
  font-weight: 500;
}
.second-li {
  margin-top: 20px;
}
.keys {
  margin-top: 5rem;
  padding: 2rem;
  padding-left: 1.5rem;
  padding-right: 3rem;
  position: fixed;
}
.l-key-top {
  padding: 10px 15px 20px;
}
.l-key-top label {
  font-size: 13px;
  color: #ffffff7a;
}
.l-key-bottom {
  padding: 16px 15px;
}
.last-img {
  height: 36px;
  margin-top: 3px;
}
/* ==================== Modal =================== */
.modal-content {
  background-color: #4b2422 !important;
  border-radius: 18px !important;
  padding: 0 1rem;
  min-height: 300px;
  display: flex;
  justify-content: center;
}

.modal-dialog {
  max-width: 450px !important;
}
.modal-open {
  padding-right: 0 !important;
}

.btn-sca {
  animation: btn-scale 0.3s;
}
@keyframes btn-scale {
  0% {
    transform: scaleX(1);
  }
  50% {
    transform: scaleX(0.85);
  }
  100% {
    transform: scaleX(1);
  }
}
/* Spinner animations */

.spinner {
  animation-name: roll;
  animation-duration: 2s;
  width: 70px;
}
@keyframes roll {
  0% {
    transform: rotate(0deg);
  }
  40% {
    transform: rotate(-360deg);
  }

  100% {
    transform: rotate(0deg);
  }
}
.okay-h {
  background-color: rgb(40, 158, 29);
  display: inline-block;
  border-radius: 10rem;
  font-size: 11px;
  width: 40px;
  height: 40px;
}
.success-dialog {
  transform: scale(0);
  height: 0;
  animation-name: diaSuccess;
  animation-delay: 1.5s;
  animation-fill-mode: forwards;
  animation-duration: 1s;
  animation: forwards;
}
.modal-open {
  padding-right: 0 !important;
}
