body {
  background-color: #200f15 !important;
  color: #fff !important;
  font-family: "Poppins", sans-serif !important;
  overflow-x: hidden;
}
.note-tick {
  display: flex;
  justify-content: center;
  flex-direction: row;
  z-index: 1000;
  position: relative;
}
.main-site {
  background-image: url("./assets/images/ggg.png");
  background-attachment: fixed;
  background-size: 100% 100vh;
}
.license {
  padding: 0 20px;
}

.col-lg-10.app-dash {
  padding-right: 0 !important;
}
.act-ac {
  z-index: 10;
  top: 10px;
  padding: 1px 10px;
  border-radius: 17px;
}
.act-ac .btn {
  font-size: 13px;
}
.act-ac{
  background-color: #ae625a;
  color: #FFF !important;
}
@media (max-width: 575.98px) {
  
}
@media (max-width: 991.98px) {
  .main-site .col-lg-2 {
    padding-right: 0 !important;
  }
  .main-site {
    background-image: url("./assets/images/mobile-main-bg.png");
    background-size: 100%;
  }
  
  .act-ac .btn {
    font-size: 10px;
  }
}
@media (min-width: 992px) {
  .d-flex.sm-di {
    display: none !important;
  }
}
