.filters-data li {
  padding: 5px;
  border-radius: 100px;
  margin: 5px;
  height: 30px;
  width: 30px;
  font-size: 14px;
}
.las-li-m {
  height: 17px;
  border-radius: 0 0 15px 15px;
}
.main-task {
  font-size: 22px;
}
.filters-data .f-all {
  background-color: #fff !important;
  color: #000 !important;
}
.main-mobile {
  padding-right: 0;
}

.main-mobile > li {
  overflow: hidden;
  background-color: #281218;
  padding-left: 17px !important;
  border-bottom: 4px solid #361b22;
  border-radius: 13px;
  padding-right: 17px;
  transition: .2s ease-in-out;
}
.main-mobile > li:hover {
  background-color: #532833;
}
.m25 {
  margin-top: 20px;
  height: 470px;
  overflow: hidden;
}
.m25:hover {
  overflow: auto;
}
/* width */
.m25::-webkit-scrollbar {
  width: 6px !important;
}

/* Track */
.m25::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(172, 171, 171) !important; 
  border-radius: 10px !important;
}
 
/* Handle */
.m25::-webkit-scrollbar-thumb {
  background: #4e1324 !important; 
  border-radius: 10px !important;
}
.main-data ul {
  padding-top: 0;
}
/* Handle on hover */
.m25::-webkit-scrollbar-thumb:hover {
  background: #641313 !important; 
}
.dif {
  padding-top: 5px;
  padding-bottom: 5px;
}
.hidden-li {
  box-shadow: 0px -12px 39px 30px #361b22;
  position: relative;
}
.success-mob {
  color: #3c8a71dc !important;
  font-size: 14px;
  margin-top: 20px;
  font-weight: bold;
}
.fa-circle {
  font-size: 5px;
}
.dateNov {
  font-size: 10px;
}
.super {
  font-size: 14px;
}
.item-price::before {
  content: " ";
  color: #fff;
  border-left: 2px solid #cabebe0a;
  left: -10%;
  top: 18%;
  height: 35px;
  position: absolute;
}
.item-price::after {
  content: " ";
  color: #fff;
  border-left: 2px solid #cabebe0a;
  left: 75%;
  top: 18%;
  height: 35px;
  position: absolute;
}
@media (max-width: 575.98px) {
  .dateNov {
    font-size: 8px;
  }
  .main-mobile {
    font-size: 12px;
    padding-top: 0;
  }
  .success-mob {
    font-size: 12px;
    margin-top: 20px;
    margin-left: -4px;
  }
  .main-task {
    font-size: 15px;
  }
  .filters-data li {
    font-size: 10px;
    margin: 0px 3px;
  }
  .filters-data button {
    background-color: #051b2acf !important;
    height: 25px;
    width: 25px;
    margin-left: 0px;
    margin-right: 0px;
    font-weight: 500 !important;
  }
  .main-mobile > li {
    padding-left: 6px !important;
  }
  .fa-circle {
    font-size: 2px;
  }
}
