.pos-img {
  position: fixed;
}

.left-logo {
  position: absolute;
  left: 14%;
  width: 54%;
  top: 12%;
}
.logout {
  position: absolute;
  top: 33%;
  z-index: 500;
  background-color: #2c141b;
  width: 64%;
  height: 273px;
  font-size: 24px;
  text-align: center;
  border-radius: 0 40px 40px 0;
}
.out {
  background-image: url("../../assets/images/page-in.png");
  background-color: transparent;
  border: 0;
  width: 23px;
  height: 27px;
  background-repeat: no-repeat;
  background-size: 100%;
  display: inline-block;
  margin-top: 5rem;
  transition: 0.2s all ease-in-out;
}
.out-w::before {
  top: 30%;
  content: " ";
  height: 20px;
  width: 6px;
  background-color: #eb5e70;
  position: absolute;
  left: 0;
  transform: skewY(41deg);
}
.img-muted {
  background-image: url("../../assets/images/page-in-2.png");
}
.out:hover {
  background-image: url("../../assets/images/page-in.png");
}
.off-btn {
  border: 0;
  background-color: transparent;
  color: rgb(68, 68, 68);
  margin-top: 2rem;
  transition: 0.2s all ease-in-out;
}
.off-btn-w::before {
  top: 57%;
  content: " ";
  height: 20px;
  width: 6px;
  background-color: #eb5e70;
  position: absolute;
  left: 0;
  transform: skewY(41deg);
}
.off-btn:hover {
  color: #fff !important;
}
/* // Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .panel {
    width: 82%;
  }
  .panel-div {
    margin-right: -37px;
  }
}

/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
}
@media (max-width: 992px) {
  .pos-img {
    position: relative;
    height: 5rem;
  }
  .logo-top {
    display: none;
  }
  .pos-img .col-lg-2 {
    padding-right: 0 !important;
  }
  .logout {
    top: 0;
    width: 36%;
    right: 0;
    height: 73px;
    border-radius: 0 0 0 40px;
    display: flex;
    justify-content: space-evenly;
    padding: 12px 30px 0;
    padding-top: 12px;
  }
  .out {
    margin-top: 5px;
  }

  .off-btn {
    margin-top: 0;
  }
  .left-logo {
    position: relative;
    left: 4%;
    width: 44px;
    top: 21%;
  }
  .out-w::before {
    top: 0;
    height: 7px;
    width: 20px;
    left: 30%;
    transform: skewX(22deg);
  }
  .off-btn-w::before {
    top: 0;
    height: 7px;
    width: 20px;
    left: 60%;
    transform: skewX(22deg);
  }
}
@media (min-width: 576px) and (max-width: 767.98px) {
  .out-w::before {
    left: 28%;
  }
  .off-btn-w::before {
    left: 58%;
  }
}
@media (max-width: 575.98px) {
  .logout {
    width: 49%;
    height: 68px;
    padding: 12px 6px 0 30px;
    padding-top: 12px;
  }
  .left-logo {
    position: relative;
    left: 5%;
    width: 45px;
    top: 13%;
  }
  .out-w::before {
    left: 32%;
  }
  .off-btn-w::before {
    left: 63%;
  }
}
