.download-title {
  margin-bottom: 1rem;
  font-size: 14px;
  color: #ffffff7a;
  margin: 20px 0 20px;
}
.download button {
  width: 100%;
  height: 138px;
  background-size: 100% 100%;
  border: 0;
  background-color: #361b22;
  border-radius: 5px;
}
.download button:hover h5 {
  color: #fff;
  text-decoration: underline;
}
.download button img {
  width: 29%;
}
.download button h5 {
  font-size: 12px;
  color: #ddd;
  margin-top: 1rem;
}
.down-div {
  width: 46px;
  height: 49px;
  left: 37%;
  border-radius: 5px;
  position: absolute;
  background-color: #46222c;
  overflow: hidden;
  opacity: 0;
}
.download button .down-div img {
  width: 59%;
  margin-top: 11px;
  position: relative;
  z-index: 5;
}
.download button:hover .down-div {
  opacity: 1;
}
.download button:hover > img {
  opacity: 0;
}

.download .windows-btn h5 {
  margin-top: 1.5rem;
}
.overlay {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  height: 0;
  background-color: rgb(129, 47, 58);
  z-index: 3;
}
.over-anim {
  animation: addOver 2s ease-in-out forwards;
}
.fa-check {
  position: relative;
  z-index: 500;
  margin-top: 8px;
}
@keyframes addOver {
 form {
   height: 0;
 } 
 to {
   height: 100%;
 }
}
/* // Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .download button img {
    width: auto;
  }
  .down-div {
    left: 35%;
  }
  .download {
    margin-bottom: 10px;
  }
}

/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
  .download button img {
    width: auto;
  }
  .down-div {
    left: 43%;
  }
}

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
  .download button img {
    width: auto;
  }
  .down-div {
    left: 43%;
  }
}

/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .download button h5 {
    font-size: 11px;
  }
  .download button {
    height: 127px;
  }
  .down-div {
    width: 43px;
    height: 46px;
  }
  .download button .down-div img {
    width: 52%;
    margin-top: 13px;
  }
  .pos-img .logo-top {
    width: 87% !important;
  }
}

/* // Extra large devices (large desktops, 1200px and up) */
