.main-dashboard {
  padding-top: 2rem;
  padding-bottom: 2rem;
  overflow: hidden;
}
.main-data {
  background-color: #361b22;
  padding: 18px;
  padding-bottom: 0;
  margin-bottom: 2rem;
  border-radius: 10px;
  box-shadow: 1px 1px 18px -9px #000000;
}
.main-data ul {
  padding-bottom: 0;
}
.btn-list > li {
  margin: 3px;
}
.btn-list > li button {
  background-color: #200f15;
  color: #fff;
  border: 0;
  padding: 5px 10px;
  width: 40px;
  display: inline-block;
  border-radius: 20px;
  font-size: 10px;
}
.btn-list > li .btn-all {
  background-color: #a44e48;
}
.btn-list > li button:focus {
  outline: 0 !important;
  background-color: #a44e48;
}
button:focus {
  outline: 0 !important;
}
.sort {
  margin-top: 30px;
  padding-right: 0 !important;
}
.sort h4 {
  font-size: 15px;
  margin-bottom: 0.5rem;
}
.sort ul {
  padding-top: 0;
  padding-right: 0;
}
.main {
  background-color: #43232b;
  display: inline-block;
  width: 100%;
  padding: 10px;
  border-radius: 10px;
}

.main .number {
  font-size: 36px;
}
.main .title {
  font-size: 12px;
}
.span-right {
  width: 50px;
  height: 50px;
  background-color: #291319;
  border-radius: 50%;
  margin-top: 10px;
}
.span-right img {
  width: 50%;
  margin-top: 10px;
  margin-left: 13px;
}
.img-success img {
  width: 57%;
  margin-top: 13px;
  margin-left: 11px;
}
.main .sp-left .number {
  transition: all 0.3s ease-in-out;
}
.main:hover .sp-left .number {
  margin-top: -5px;
  margin-bottom: 5px;
  color: #27b87d;
}
.main:hover .sp-uni .number {
  margin-top: -5px;
  margin-bottom: 5px;
  color: #a44e48;
}
.main span img {
  transition: all 0.3s ease-in-out;
}
.main:hover span img {
  margin-top: 9px !important;
}
.main:hover .sp-uni img {
  margin-top: 5px !important;
}
.item-price span.text-muted {
  margin-bottom: -4px;
  margin-top: 5px;
  color: #ffffff7a !important;
}
.text-muted .dateNov {
  color: #ffffff7a !important;
}
.text-muted small {
  color: #ffffff7a !important;
}
.dif .text-muted {
  margin-top: -5px;
}
/* // Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .sort {
    order: 3;
    padding-right: 13px !important;
    margin-top: 22px;
  }
  .sort ul {
    float: right;
    margin-top: -9px;
  }
  .sort h4 {
    float: left;
  }
  .btn-list > li button {
    padding: 5px 12px;
    width: 39px;
    font-size: 11px;
  }
  .sort h4 {
    font-size: 14px;
  }
  .span-right {
    width: 39px;
    height: 39px;
  }
  .main .number {
    font-size: 25px;
  }
  .main .title {
    font-size: 9px;
  }
  .span-right img {
    width: 50%;
    margin-top: 7px;
    margin-left: 10px;
  }
  .img-success img {
    margin-top: 10px;
    margin-left: 8px;
  }
  .t-check {
    padding-right: 10px !important;
  }
  .t-fail {
    padding-left: 10px !important;
  }
  .dif .col-8 {
    max-width: 55%;
  }
  .dif .col {
    width: 20% !important;
  }
  .item-price::before {
    left: -4% !important;
  }
  .item-price::after {
    left: 97% !important;
  }
  .super {
    font-size: 11px !important;
    margin-bottom: 4px;
  }
}

/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
  .sort {
    order: 3;
    padding-right: 15px !important;
  }
  .sort ul {
    float: right;
    margin-top: -9px;
  }
  .sort h4 {
    float: left;
  }
  .btn-list > li button {
    padding: 5px 12px;
    width: 60px;
    font-size: 14px;
  }
  .sort h4 {
    font-size: 19px;
  }
  .item-price::before {
    left: -4% !important;
  }
  .item-price::after {
    left: 90% !important;
  }
  .dif .col-8{
    max-width: 64.666667%;
  }
}

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
  .sort {
    order: 3;
  }
  .sort ul {
    float: right;
    margin-top: -9px;
    margin-right: 20px;
  }
  .sort h4 {
    float: left;
  }
  .btn-list > li button {
    padding: 5px 12px;
    width: 60px;
    font-size: 14px;
  }
  .sort h4 {
    font-size: 19px;
  }
  
}

/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .btn-list > li button {
    width: 32px;
    padding: 4px 10px;
  }
  .main .number {
    font-size: 27px;
  }
  .main .title {
    font-size: 9px;
  }
  .span-right {
    width: 42px;
    height: 42px;
    margin-top: 2px;
  }
  .img-success img {
    margin-top: 11px !important;
    margin-left: 9px !important;
  }
  .span-right img {
    margin-top: 8px;
    margin-left: 11px;
  }
  .sort h4 {
    font-size: 13px;
    margin-bottom: 0.5rem;
  }
  .sort {
    margin-top: 32px;
  }
  .m25 {
    margin-top: 0px;
  }
}

/* // Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
}
